<template>
  <video-player
    class="video-player-box"
    ref="videoPlayer"
    :options="playerOptions"
    :playsinline="true"
  >
  </video-player>
</template>

<script>
// https://gossv.vcg.com/cmsUploadVideo/creative/1/7.mp4
// require styles
import "video.js/dist/video-js.css";
import { videoPlayer } from "vue-video-player";

export default {
  name: "BvVideo",
  components: {
    videoPlayer
  },
  props: {
    config: {
      type: Object,
      default: () => ({
        url:
          "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm",
        muted: true,
        autoplay: false,
        loop: false,
        fluid: false,
        notSupportedMessage: "",
        poster: "https://img01.yzcdn.cn/vant/apple-3.jpg"
      })
    }
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    },
    playerOptions() {
      return {
        muted: this.config.muted, // 
        autoplay: this.config.autoplay, // 
        loop: this.config.loop, // 
        fluid: this.config.fluid, // 
        notSupportedMessage: this.config.notSupportedMessage, // Video.js
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 
        poster: this.config.poster,
        sources: [
          {
            type: "video/mp4",
            src: this.config.url
          }
        ]
      };
    }
  }
};
</script>

